import React from 'react';

const Rank = ({name, entries}) => {
    console.log('rank',name, entries);
    return(
        <div >
            <div className="white f3">
                {`${name}, your entry count is....`}
            </div>
            <div className="white f1">
                {entries}
            </div>
        </div>
    );
}

export default Rank;